.friendList {
  text-decoration: none;
  list-style: none;
  padding: 0 0;
}

.item {
  border: 1px solid aqua;
  display: flex;
  align-items: center;
  box-shadow: 1px 2px 8px 4px rgb(197, 191, 191);
  /* justify-content: center; */
}

.item:not(:last-child) {
  margin-bottom: 20px;
}

.statusOf {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: red;
  margin-left: 10px;
}

.statusOn {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: green;
  margin-left: 10px;
}

.avatar {
  margin: 0 20px;
}

.name {
  font-size: 30px;
}
